<template>
    <el-container>
        <el-header height="40" class="header">
            <el-row>
                <el-col :span="18">八维研修学院</el-col>
                <el-col :span="6" class="username">姓名：{{username}}</el-col>
            </el-row>
        </el-header>
        <el-header class="sub-header">
            <el-row class="subject">
                <el-col :span="12" class="subject-name">第{{qindex+1}}题：{{a.title}}</el-col>
                <el-col :span="6" class="layout-set">
                    <el-button size="mini" @click="layoutConfig(8,16)">1:2</el-button>
                    <el-button size="mini" @click="layoutConfig(12,12)">1:1</el-button>
                    <el-button size="mini" @click="layoutConfig(16,8)">2:1</el-button>
                </el-col>
                <el-col :span="6" class="left-time">剩余时间：{{leftTime}}</el-col>
            </el-row>
        </el-header>
        <el-main>
            <el-row class="hp100 qa-wrap">
                <el-col :span="layoutLeft" class="hp100 q-r">
                    <question :q="q"></question>
                </el-col>
                <el-col :span="layoutRight" class="hp100">
                    <answer @answer="saveAnswer" :a="a"></answer>
                </el-col>
            </el-row>
        </el-main>
        <el-footer>
            <el-row>
                <el-col :span="2">
                    <el-dropdown trigger="click">
                        <el-button class="el-dropdown-link" size="small">题目列表</el-button>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="(q,i) in qlist" :key="i">
                                <span
                                    @click="qindex=i"
                                    class="qlist-item"
                                >{{q.exam_name}}：{{q.questions_stem.slice(0,8)}}...</span>
                            </el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-col>
                <el-col :span="4" :push="2">
                    <el-button size="small" @click="prev">上一题</el-button>
                    <el-button size="small" @click="next">下一题</el-button>
                </el-col>
                <el-col :span="2" :push="16">
                    <el-button
                        size="small"
                        type="primary"
                        :disabled="cantSubmit"
                        @click="submitAnswer"
                    >提交</el-button>
                </el-col>
            </el-row>
        </el-footer>
    </el-container>
</template>

<script>
import answer from "@/components/answer";
import question from "@/components/question";
export default {
    name: "index",
    data: function() {
        return {
            layoutLeft: 8,
            layoutRight: 16,
            qlist: [],
            qindex: 0,
            username: "无信息",
            alist: [],
            exam_exam_id: null,
            starttime: 1553151000000,
            leftTime: new Date().toLocaleTimeString(),
            endtime: 1553151100000,
            cantSubmit: false
        };
    },
    computed: {
        q() {
            return this.qlist[this.qindex] || {};
        },
        a() {
            return this.alist[this.qindex] || {};
        }
    },
    components: {
        answer,
        question
    },
    methods: {
        countDown(starttime, endtime) {
            //结束时间
            var endDate = new Date(endtime);
            //当前时间
            var nowDate = new Date(starttime);
            //相差的总秒数
            var totalSeconds = parseInt((endtime - starttime) / 1000);
            if (totalSeconds <= 0) {
                this.leftTime = `考试时间已到，请提交答案。`;
                this.submitFunc();
                return;
            }
            //天数
            var days = Math.floor(totalSeconds / (60 * 60 * 24));
            //取模（余数）
            var modulo = totalSeconds % (60 * 60 * 24);
            //小时数
            var hours = Math.floor(modulo / (60 * 60));
            modulo = modulo % (60 * 60);
            //分钟
            var minutes = Math.floor(modulo / 60);
            //秒
            var seconds = modulo % 60;
            //输出到页面
            this.leftTime = `${hours} 时 ${minutes} 分 ${seconds} 秒`;

            //延迟一秒执行自己
            let timer = setTimeout(() => {
                starttime += 1000;
                this.countDown(starttime*1, endtime*1);
                clearTimeout(timer);
            }, 1000);
        },
        layoutConfig(left, right) {
            this.layoutLeft = left;
            this.layoutRight = right;
        },
        prev() {
            this.qindex -= 1;
            if (this.qindex < 0) {
                this.qindex = this.qlist.length - 1;
            }
        },
        next() {
            this.qindex += 1;
            if (this.qindex >= this.qlist.length) {
                this.qindex = 0;
            }
        },
        unfinished() {
            return new Promise((resolve, reject) => {
                let notanswer = this.alist.filter(
                    item =>
                        !item.student_answer && item.student_answer.trim() == ""
                );
                if (notanswer.length > 0) {
                    this.$confirm(
                        `还有${notanswer.length}道题未作答，确定交卷吗？`,
                        "提示",
                        {
                            confirmButtonText: "确定",
                            cancelButtonText: "取消",
                            type: "warning"
                        }
                    )
                        .then(() => {
                            resolve("ok");
                        })
                        .catch(() => {
                            resolve("no");
                        });
                } else {
                    resolve("ok");
                }
            });
        },
        async submitFunc() {
            let collectData = {
                exam_exam_id: this.exam_exam_id,
                questions: this.alist,
                start_time: this.starttime * 1,
                end_time: this.endtime * 1
            };
            this.cantSubmit = true;
            let res;
            try {
                res = await this.$http.post(`/exam/student`, collectData);
            } catch (e) {
                this.$message({
                    showClose: true,
                    message: `something bad happened, please try again.`,
                    type: "error"
                });
                this.cantSubmit = false;
            }
            let that = this;
            this.$alert(res.msg, "提示", {
                confirmButtonText: "确定",
                callback: action => {
                    that.cantSubmit = true;
                }
            });
        },
        async submitAnswer() {
            let isGoAhead = await this.unfinished();
            if (isGoAhead === "no") return;
            this.submitFunc();
        },
        saveAnswer(e) {
            this.alist = this.alist.map(item => {
                if (item.questions_id == e[1]) {
                    item.student_answer = e[0];
                }
                return item;
            });
        },
        handleErr(promise) {
            if (!promise || !Promise.prototype.isPrototypeOf(promise)) {
                return new Promise((resolve, reject) => {
                    reject(new Error("requires promises as the param"));
                }).catch(err => {
                    return [err, null];
                });
            }
            return promise
                .then(function() {
                    return [null, ...arguments];
                })
                .catch(err => {
                    this.$message({
                        message: `Sorry, Internal Server Error`,
                        type: "error"
                    });
                    return [err, null];
                });
        }
    },
    async mounted() {
        let res = await this.$http.get(`/student/info`);
        if (res.code == 1) {
            this.username = res.data.student_name;
            this.exam_exam_id = res.data.exam_exam_id;
            let startTime = res.data.start_time || this.starttime;
            let endTime = res.data.end_time || this.endtime;
            this.countDown( startTime*1, endTime*1 );
            let [err, qs] = await this.handleErr(
                this.$http.get(`/exam/unstart/${res.data.exam_exam_id}`)
            );
            if (err) return;
            if (qs.code === 1) {
                this.qlist = qs.data.questions;
                this.alist = qs.data.questions;
                return;
            }
            this.$message({
                showClose: true,
                message: `${qs.msg}，请重试`,
                type: "error"
            });
        } else {
            this.$message({
                showClose: true,
                message: `${res.msg}，请重试`,
                type: "error"
            });
        }
    }
};
</script>

