<template>
    <div class="answer-wrapper hp100">
        <el-row class="answer-bar">
            <el-col :span="20">答题框：</el-col>
            <el-col :span="4">
                <el-select
                    size="small"
                    v-model="codetype"
                    @change="languageChange"
                    placeholder="请选择"
                >
                    <el-option
                        v-for="item in codetypelist"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value"
                        :disabled="item.disabled"
                    ></el-option>
                </el-select>
            </el-col>
        </el-row>
        <el-row class="answer-box">
            <el-col class="hp100">
                <textarea ref="textarea" name id cols="30" rows="10"></textarea>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import CodeMirror from "codemirror";
import "codemirror/mode/javascript/javascript";
import "codemirror/mode/htmlmixed/htmlmixed";
import "codemirror/lib/codemirror.css";
import 'codemirror/theme/monokai.css'
export default {
    name: "answer",
    props: {
        a: {
            type: Object,
            default: function() {
                return {};
            }
        }
    },
    data() {
        return {
            editor: null,
            codetypelist: [
                {
                    value: "javascript",
                    label: "JavaScript"
                },
                {
                    value: "html",
                    label: "HTML",
                    disabled: false
                }
            ],
            codetype: "JavaScript"
        };
    },
    watch:{
      a(val){
        this.editor.setValue(val.student_answer || "")
      }
    },
    methods: {
        codeChange(answer) {
            this.$emit("answer", [answer, this.a.questions_id]);
        },
        languageChange(item) {
            this.editor.setOption("mode", item);
        }
    },
    mounted() {
        // use editor object...

        let editor = this.editor = CodeMirror.fromTextArea(this.$refs.textarea, {
            lineNumbers: true,
            mode: "javascript",
            lineWrapping: true,
            theme:'monokai', //编辑器主题
            smartIndent:true
        });

        editor.on("change",(answer)=>{
          //事件触发后执行事件
          this.$emit("answer", [answer.getValue(), this.a.questions_id]);
        });

        editor.focus();
        editor.setSize("100%", "100%");
    }
};
</script>

<style scoped>
.answer-wrapper {
    line-height: 32px;
    padding: 10px;
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
}
.vue-codemirror-wrap {
    border: 1px solid #ccc;
    line-height: 1.4;
    height: 100%;
}
.CodeMirror {
    border: 1px solid #eee;
    height: auto;
}
.answer-bar {
    line-height: 39px;
}
.answer-box {
    flex: 1;
}
.el-row:last-child {
    margin-bottom: 0;
}
</style>
