<template>
  <el-container>
    <el-header>网站考试系统（学生端）</el-header>
    <el-main
      v-loading="loading"
        element-loading-text="拼命加载中"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <div class="form-box">
        <el-form :model="validateForm" ref="validateForm" class="demo-ruleForm">
          <el-form-item
            prop="stuNum"
            :rules="[
              { required: true, message: '学号不能为空'},
              { type: 'number', message: '学号必须为数字值'}
            ]"
          >
            <el-input
              type="stuNum"
              v-model.number="validateForm.stuNum"
              autocomplete="off"
              placeholder="学号"
            ></el-input>
          </el-form-item>
          <el-form-item
            prop="password"
            :rules="[
              { required: true, message: '请输入密码'}
            ]"
          >
            <el-input
              type="password"
              v-model="validateForm.password"
              autocomplete="off"
              show-password
              placeholder="密码"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('validateForm')">登陆</el-button>
            <el-button @click="resetForm('validateForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-main>
    <el-footer>Copyright © 2019 - 2020 网站工程学院. All Rights Reserved.</el-footer>
  </el-container>
</template>

<script>
import {setCookie} from '../utils/utils'
export default {
  name: "login",
  data() {
    return {
      loading: false,
      validateForm: {
        stuNum: 152031000541,
        password: "1609Azs!"
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.loading = true;
          this.$http
            .post("/student/login", {
              student_id: this.validateForm.stuNum.toString(),
              student_pwd: this.validateForm.password
            })
            .then(
              res => {
                this.loading = false;
                if (res.code == 1) {
                  setCookie('student_token',res.token,3)
                  
                  this.$router.push("/");
                } else {
                  this.$alert("用户名或密码有误！", "提示", {
                    confirmButtonText: "重新输入"
                  })
                }
              },
              err => {
                this.loading = false;
                this.$message({
                  showClose: true,
                  message: `${err}，请重试`,
                  type: "error"
                });
              }
            );
        } else {
          this.$message({
            showClose: true,
            message: "请输入账号或密码",
            type: "warning"
          });
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>

