<template>
    <div class="question-wrapper">
        <el-tabs v-model="activeName" @tab-click="handleClick" class="subject-content">
            <el-tab-pane label="题目描述" name="first">
              <div class="qcontent" ref="qcontent"></div>
            </el-tab-pane>
            <el-tab-pane label="其他" name="second">
              <el-popover placement="right" trigger="click">
                <div slot="reference"><img class="qimg" :src="imgurl" alt=""></div>
                <img :src="imgurl" alt="">
              </el-popover>
            </el-tab-pane>
            <el-tab-pane label="评论" name="third">角色管理</el-tab-pane>
        </el-tabs>
    </div>
</template>

<script>
import markdown from 'markdown-it'
export default {
    name:'question',
    data() {
      return {
        activeName: 'first',
        md:null,
        imgurl:'http://img1.juimg.com/170707/355792-1FFF0502258.jpg'
      }
    },
    props:{
      q:{
        type: Object,
        default: function(){
          return {questions_stem:''}
        }
      }
    },
    methods: {
      handleClick(tab, event) {
        //console.log(tab, event);
      }
    },
    watch:{
      q:function(a,b){
        //val.questions_stem
        let regexp = /!\[alt\]\((.*)\)/g;
        if(regexp.exec(a.questions_stem)){
          console.log(regexp.exec(a.questions_stem))
          this.imgurl = regexp.exec(a.questions_stem)[1];
          a.questions_stem = a.questions_stem.replace(regexp,'## 请在 “其他“ 中查看图片。 \n \n')
        }
        let qcontent = '# '+a.questions_type_text+' \n \n \n'+a.questions_stem
        this.$refs.qcontent.innerHTML = this.md.render(qcontent)
      }
    },
    mounted(){
      this.md = markdown({
        html: true,
        linkify: true,
        typographer: true
      })

    }
}
</script>

